<template>
  <body>
    <div class="body">
      <div class="nav ">
        <div class="nav_box fix">
          <div class="nav_switch l">
            <ul class="ul fix">
              <li>首页</li>
              <li>前端</li>
              <li>学习</li>
              <li>小说</li>
            </ul>
          </div>
          <div class="nav_search r">
            <input type="text" placeholder="请输入搜索内容" v-on:keyup.enter="cc">
          </div>
        </div>
      </div>
    </div>
  </body>
</template>
<script>
export default {
  name: '',
  data() {
      return {}
  },
  methods:{
    cc: function () {
      alert(11)
    },
  }
}
</script>
<style lang="less" type="text/less" scoped>
  @import "../style/base";
.body{
  .nav{
    height: 40px;
    line-height: 40px;
    /*border-top: 2px solid yellowgreen;*/
    background-color: #b4a078;
    font-size: 14px;
    .nav_box{
      margin: 0 auto;
      width: 80%;
      .nav_switch{
        color: white;
        .ul{
          li{
            width: 100px;
            float: left;
          }
          li:hover{
            background-color: white;
            cursor:pointer;
            color: rgba(254,67,101,1);
          }
        }
      }
      .nav_search{
        input{
          padding-left: 4px;
          width: 200px;
          height: 24px;
        }
      }
    }
  }
}
</style>



